				"html": "<p><snippet>譯: <a href=\"https://conversionxl.com/blog/saas-signup-flows/\">3 Common SaaS Sign-Up Flows (and a Friction-Based Analysis)</a></snippet></p>\n<p>非心理系出身，若是有專有名詞翻錯，請指正。</p>\n<p>摩擦</p>\n<blockquote>\n<p>當參訪者嘗試完成一個動作的時候所遇到的心理上的反抗，是轉換率殺手。</p>\n</blockquote>\n<p>你可以試著優化產品的價值主張與call to action buttons(行動按鈕)，但若是你的註冊流程包涵了太多的摩擦，你就會流失你的客戶。</p>\n<p>減少摩擦就是最有效率增加轉換率的方式。然而，如果你只是胡亂猜測的話，是很難去辨識摩擦來源的。</p>\n<p>摩擦會出現在網頁的各處，我們這邊只講註冊流程，也是通常摩擦最容易發生的地方。</p>\n<h3>摩擦與註冊流程</h3>\n<p>摩擦就是使用者必須要多精通註冊流程的測量。</p>\n<p>雖然因為多樣性與客觀性很難去量化標準，但還是可以使用三種因子去做評估</p>\n<ol>\n<li>\n<p>完成步驟 - 使用者會經過的一連串的頁面與步驟的數量。</p>\n</li>\n<li>\n<p>資訊代價 - 使用者必須填寫的資訊數量。</p>\n</li>\n<li>\n<p>付出投資 - 使用者必須做的決定與額外須要完成的活動。(像是email認証，CAPTCHA，其他...)</p>\n</li>\n</ol>\n<p>這邊有幾種註冊流程的設計方式，他們各自有優缺點。但是最終的目的都是為了減少摩擦，最大價值化客戶與利潤。</p>\n<p>待續....</p>",
